.header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.625rem 1.25rem 0.3125rem;
  background-color: #0a0a0a;
}

.header__webTitle {
  font-family: 'Righteous', cursive;
  margin-top: 0rem;
  margin-bottom: 0.3125rem;
  letter-spacing: 3px;
  font-size: 1.5625rem;
  display: inline-block;
  color: white;
  margin-bottom: 5px;
}

.header__form {
  margin-top: 0.8125rem;
  margin-bottom: 0.8125rem;
  display: flex;
  justify-content: left;
  align-items: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__form__input {
  flex-grow: 2;
  max-width: 600px;
  height: 1.75rem;
  padding-left: 0.625rem;
  margin-right: 0.625rem;
  border: 0;
  border: 0.09375rem solid #309bd1;
  background: linear-gradient(45deg, #495643 0%, #49565d 100%);
  border-radius: 0.625rem;
}

.header__form__submit {
  height: 1.75rem;
  width: 5.625rem;
  font-weight: 700;
  text-transform: capitalize;
  color: #fff;
  border: none;
  border-radius: 0.625rem;
  background-color: #309bd1;
  cursor: pointer;
}

.main {
  height: 0%;
}

.main__lists {
  height: 0;
  list-style: none;
  display: flex;
  justify-content: left;
  flex-direction: column;
}

.main__lists__searchList {
  height: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  order: 2;
  margin-top: 0rem;
  padding: 1.25rem;
  margin-top: 0rem;
  padding-top: 0.9375rem;
  margin: 0;
  padding: 0;
}

.main__lists__searchList-searchListTitle {
  margin-top: 2.1875rem;
  margin-bottom: 0.625rem;
  text-align: center;
  color: black;
  text-transform: uppercase;
  font-family: 'Righteous', cursive;
  letter-spacing: 3px;
  font-size: 1.25rem;
}

.main__lists__searchList-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0 15px;
}

.main__lists__searchList-container-liContainer {
  text-transform: uppercase;
  border-radius: 5px;
  margin: 0.9375rem 0.75rem 1.25rem;
  background-color: transparent;
  color: white;
  display: flex;
  flex-direction: column;
  width: 180px;
  cursor: pointer;
}

.main__lists__searchList-container-liContainer-span {
  font-family: "Roboto", sans-serif;
  text-align: center;
  font-weight: 700;
}

.main__lists__searchList-container-liContainer-span img {
  width: 180px;
  height: 240px;
}

.main__lists__favoriteList {
  order: 1;
  list-style: none;
  padding: 0;
  display: flex;
  align-items: center;
  padding: 1.5625rem 10px;
  margin-top: 0rem;
  border-bottom: white 0.3125rem double;
  flex-direction: column;
  height: fit-content;
  text-transform: uppercase;
  font-size: 0.9375rem;
}

.main__lists__favoriteList-listTitle {
  text-transform: uppercase;
  margin-left: 0.9375rem;
  color: black;
  margin: 0.625rem 0rem 0.9375rem;
  text-align: center;
  font-family: 'Righteous', cursive;
  letter-spacing: 3px;
  font-size: 1.25rem;
}

.main__lists__favoriteList-favoriteItem {
  border-radius: 0.3125rem;
  width: fit-content;
  height: fit-content;
  margin: 2.5rem 0rem 0.625rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 0.9375rem;
  background-color: rgba(255, 255, 255, 0.281);
  color: #000000e3;
  box-shadow: 2px 2px 1px #0c0c0c;
}

.main__lists__favoriteList-favoriteItem-header {
  background-color: black;
  display: flex;
  flex-direction: row;
}

.main__lists__favoriteList-favoriteItem-header-span {
  padding: 0rem 0rem 0.9375rem;
  font-size: 0.9375rem;
  text-align: center;
  font-weight: 600;
}

.main__lists__favoriteList-favoriteItem-header-closeElement {
  cursor: pointer;
  align-self: flex-end;
  padding: 0.5rem 0.3125rem 0.3125rem;
  text-align: right;
  color: black;
}

.main__lists__favoriteList-favoriteItem-header-closeElement:hover {
  transform: scale(1.15);
  text-shadow: 0.0625rem 0.0625rem orange;
}

.main__lists__favoriteList-favoriteItem-header-image {
  margin: auto;
  width: 180px;
  height: 210px;
}

.favorite {
  background-color: white;
  color: #000000e3;
  box-shadow: 2px 1px 2px #0c0c0c;
}

* {
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

body {
  margin: 0;
  height: 100vh;
  width: 100%;
  background-color: #607d8b;
}

@media all and (min-width: 560px) {
  .header__webTitle {
    font-size: 2.1875rem;
  }

  .header__form {
    width: 90%;
  }

  .main__lists {
    flex-direction: row;
  }

  .main__lists__favoriteList {
    border-right: white 0.3125rem double;
    border-bottom: none;
  }

  .main__lists__favoriteList-favoriteItem {
    height: auto;
    max-width: 11.625rem;
    padding: 0rem 0rem 0rem;
  }

  .main__lists__favoriteList-favoriteItem-header-closeElement {
    padding: 0.5rem 0.3125rem 0.3125rem;
  }

  .main__lists__favoriteList-favoriteItem-header-image {
    max-width: 9.0625rem;
  }
}
